export default {

	props: {
		title: String,
		saveTitle: String
	},

	data()
	{
		return {
			isShow: false
		};
	},

	computed: {

		modalClasses()
		{
			return {
				'fade': !this.isShow,
				'show': this.isShow
			};
		}

	},

	mounted()
	{
		window.addEventListener('keyup', e => {

			if(this.isShow && (e.key=='Escape' || e.key=='Esc' || e.keyCode==27)) 
			{
				this.hide();
			}

		});
	},

	methods: {

		show()
		{
			if (this.isShow)
			{
				return;
			}

			this.isShow = true;
			this.$nextTick(() => {
				this.$refs.modal.focus()
			});
		},

		hide()
		{
			if (!this.isShow)
			{
				return;
			}

			this.isShow = false;
		}

	}

}
